@import url("https://fonts.googleapis.com/css?family=Material+Icons");

body {
    min-width: 550px;
    overflow-x: scroll;
    font-family: var(--sl-font-sans);
}
h1, h2, h3, h4, h5, h6, p, span {
    font-family: var(--sl-font-sans);
    font-weight: var(--sl-font-weight-normal);
    margin: 0;
    padding: 0;
}

ul {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
}

ul li {
    list-style: none;
    padding: 0;
    margin: 0.125rem 0.5rem;
}

a {
    text-decoration: none;
    line-height: 1.33;
    color: inherit;
    display: inline-block;
    padding: 0;
}

nav > * {
    padding-left: 5px;
}

a.active-link {
    color: var(--sl-color-primary-700);
}

a:not(.active-link):hover {
    color: var(--sl-color-primary-500);
}

p {
    padding-top: 10px;
    padding-bottom: 10px;
}

sl-alert {
    margin-top: 10px;
}

.theme {
    font-size: 1.5rem;
    position: absolute;
    top: 10;
    right: 70;
    z-index: 100;
}

.github {
    font-size: 1.5rem;
    position: absolute;
    top: 10;
    right: 30;
    z-index: 100;
}

div.nav {
    overflow-x: hidden;
}
.overview {
    display: flex;
    flex-direction: row;
    padding: 5px;
    align-items: flex-end;
}

.overview h2 {
    padding: 0;
    margin: 0;
}

.overview span {
    color: var(--sl-color-primary-600);
    padding-bottom: 3px;
    margin-left: 2px;
}

.storybook {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}


.component-list {
    padding-left: 10px;
}

.component-container {
    padding-left: 5px;
}

.page-container {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

.page {
    height: 100%;
    width: 100%;
}

.page-hidden {
    visibility: hidden;
    position: absolute;
    top: -100000px;
    left: -100000px;
}

.page-content {
    display: flex;
    flex-direction: column;
    /* max-width: 500px; */
    margin: auto;
    padding: 25px;
}


.description {
    margin: 0px;
    padding: 0px;
}

.description code {
    font-family: var(--sl-font-mono);
    font-weight: var(--sl-font-weight-light);
    color: var(--sl-color-primary-600);
}

.code {
    color: var(--sl-color-primary-600);
}

.mono {
    font-family: var(--sl-font-mono);
    font-weight: var(--sl-font-weight-light);
}

div.description > * {
    margin-bottom: 15px;
}

.description li {
    margin-left: 10px;
    padding: 0px;
}

.description p {
    padding: 0px;
}

.attributes {
    display: flex;
    flex-direction: row;
    /* align-items: center; */
}

.attributes > * {
    margin-top: 5px;
    margin-right: 15px;
}

.link {
    color: var(--sl-color-primary-600);
}

.align-center {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.bold {
    font-weight: var(--sl-font-weight-bold);
}

.mt10 {
    margin-top: 10px;
}
